 #ph-buttons {
	width: 100%;
	/*max-width: 960px;
	max-width: calc(100% - 320px);
	margin-top: 50px;*/
	text-align: right;
	position: relative;
	display: block;
	overflow: hidden;
}
.ph-edit-btn,
.ph-btn {
	position: relative;
	clear: none;
	margin: 10px 0;
	border: 1px solid;
	border-radius: 6px;
	font-weight: 700;
	font-style: normal;
	padding: 10px 20px;
}
.ph-edit-btn i {
	display: inline-block;
	font-style: normal;
}
#ph-buttons #ph-toggle-editor {
	float: left;
	color: #454545;
	border-color: transparent;
	background-color: #FFFFFF;
}
#ph-buttons #ph-toggle-editor:active {
	color: #FFFFFF;
	background-color: #454545;
	/*border-color: #454545;*/
}
#ph-buttons #ph-create {
	float: right;
	color: #FF9000;
	border-color: transparent;
	background-color: #FFFFFF;
	text-align: center;
	margin-left: 10px;
}
#ph-buttons #ph-create:active {
	border-color: #FF9000;
}
#ph-buttons #ph-save {
	float: right;
	color: #FFFFFF;
	background-color: #00B16A;
}
#ph-buttons #ph-save:active {
	color: #00B16A;
	border-color: #00B16A;
	background-color: #FFFFFF;
}
.fullPage .ph-edit-btn {
	display: none;
}
#ph-wrapper .CodeMirror.CodeMirror {
	position: absolute;
	border: 1px solid #CCCCCC;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: auto;
	height: auto;
}
#ph-wrapper .CodeMirror.CodeMirror,
#ph-wrapper #input textarea {
	font-family: "Ubuntu Mono", "UbuntuMono", "Lucida Console", Consolas, "Lucida Grande", "Liberation Mono", Menlo, Courier, monospace !important;
}
#ph-wrapper .CodeMirror pre {
	line-height: 1.5;
}
#ph-wrapper .CodeMirror-scroll {
	padding: 20px;
}
#ph-wrapper .cm-header-1 {
	font-size: 2em;
}
#ph-wrapper .cm-header-2 {
	font-size: 1.75em;
}
#ph-wrapper .cm-header-3 {
	font-size: 1.5em;
}
#ph-wrapper .cm-header-4 {
	font-size: 1.3em;
}
#ph-wrapper .cm-header-5 {
	font-size: 1.2em;
}
#ph-wrapper .cm-header-6 {
	font-size: 1.15em;
}
#ph-wrapper .cm-quote {
	color: #90A959;
	font-style: italic;
	margin: 0;
	padding-left: 3em;
	border-left: .5em #EEEEEE solid;
}
 #ph-side-nav {
	transform: translateZ(0);
}
#ph-nav {
	/*color: #FAFCFE;*/
	background-color: #0072C6;
	position: relative;
	float: left;
	clear: none;
	width: 320px;
	height: 100%;
	overflow: hidden;
	font-family: Roboto, "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
	padding-top: 40px;
	padding-bottom: 40px;
	border-radius: 16px 0 0 16px;
}
#ph-nav .header,
#ph-nav .logo,
#ph-nav .text {
	display: block;
	text-align: center;
	margin-left: auto;
	margin-right: auto;
	line-height: normal;
}
#ph-nav .header {
	padding-top: 10px;
	padding-bottom: 10px;
}
#ph-nav .text {
	font-size: 21px;
}
#ph-nav .text small {
	font-size: .65em;
}
#ph-nav a {
	/*transition: font .03s ease-out;*/
	color: #FAFAFA;
}
#ph-nav a:hover > .link-title {
	text-decoration: underline;
}
#ph-nav a.active:hover > .link-title/*,
#ph-nav a:not(.active):active > .link-title*/ {
	/*color: #81B3D7;*/
	/*color: #C7C7C7;*/
	text-decoration: none;
}
#ph-nav .link .icon::before {
	margin: 0;
	position: absolute;
	/*top: 50%;
	transform: translateY(-50%);*/
}
/***Special site links***/
#ph-site-pages {
	width: 100%;
	border: 1px solid #DB84E1;
	margin: 5px auto 10px;
	padding-left: 20px;
	padding-right: 20px;
	text-align: center;
	display: none;
}
#ph-nav ul {
	margin: 0;
}
#ph-nav ul,
#ph-nav li {
	padding-left: 0;
}
#ph-nav li a {
	width: 100%;
	display: block;
	position: relative;
}
/*#ph-nav .ph-btn a:hover {
	text-decoration: none;
}
*//*#ph-nav .ph-page,*/
#ph-nav .ph-rabbit-hole {
	background-color: #133E6F;
}
#ph-nav .ph-level-1 {
	color: #FFFFFF;
	font-size: 25px;
	margin: 0;
	line-height: 1.3;
	padding: 15px 20px 15px 40px;
}
#ph-nav .ph-level-2 {
	font-size: 15px;
	padding: 5px 45px 5px 20px;
	line-height: 1.6;
}
#ph-nav .ph-level-3 {
	color: #FAFCFE;
	font-size: 13px;
	line-height: 1.6;
	padding: 7px 30px 7px 60px;
}
#ph-nav .ph-rabbit-hole {
	list-style-type: none;
	text-align: left;
	padding: 8px 0;
	margin: 0 60px;
	width: calc(100% - 120px);
}
#ph-nav .ph-level-4 {
	color: #FFFFFF;
	font-size: 13px;
	margin: 0 auto;
	padding: 3px 5px;
	max-width: 85%;
	line-height: 1.2;
}
#ph-nav .active {
	transition: font .03s ease-out;
	font-weight: bold;
	text-shadow: 0 1px 1px #222222;
}
#ph-nav .active > .place {
	position: absolute;
	right: 0; /*17px;*/
	display: inline-block;
	width: 0;
	height: 0;
	border-top: 10px solid transparent;
	border-bottom: 10px solid transparent;
	border-right: 10px solid #FAFCFE;
}
#ph-nav .ph-rabbit-hole .active .place,
#ph-nav .ph-sub-parent .active .place {
	display: none;
}
#ph-nav .ph-parent a .dropper:after {
	/*content: "\2235";*/
	/*content: "\23F7";*/
	position: absolute;
	right: 35px;
	text-shadow: 0 1px 0 #133E6F;
}
#ph-nav .toggle-menu {
	position: absolute;
	right: 35px;
	color: #FFFFFF;
	vertical-align: middle;
}
#ph-nav .toggle-menu::before {
	width: 24px;
	height: 24px;
	text-align: center;
	vertical-align: middle;
}
#ph-nav .toggle-menu::before,
#ph-nav a:active .toggle-menu::before {
	border: 1px solid transparent;
	border-radius: 3px;
	font-weight: bold;
	text-shadow: none;
}
#ph-nav .toggle-menu:hover::before {
	border-color: #FFFFFF;
	text-shadow: 0 1px 0 #133E6F;
}
#ph-nav .ph-sub-parent {
	background: transparent;
	font-variant: small-caps;
}
#ph-nav .ph-sub-parent .active,
#ph-nav .ph-sub-parent a.active:hover {
	text-decoration: underline;
}
#ph-nav .ph-opened > a {
	text-shadow: 0 1px 1px #222222;
}
#ph-nav .link-title {
	display: inline-block;
}
#ph-nav .ph-program .link-title {
	padding-left: 20px;
}
#ph-nav .ph-level-3 .link-title {
	border-left: 1px solid #FAFAFA;
	padding-left: 7px;
}
#ph-nav .ph-level-3.active .link-title {
	/*border-left-color: #FAFAFA;*/
	border-left-width: 2px;
}
 /**
 * Thanks to codrops/tympanus.net for this one.
 * http://tympanus.net/codrops/2014/09/02/tab-styles-inspiration/
 */#ph-tabs {
	position: relative;
	overflow: hidden;
	margin:0 auto;
	width: 60%;
	font-weight: 300;
	font-size: 1em;
}
.fullPage #ph-tabs {
	margin: 20px auto 0;
	width: 100%;
	font-size: 1.25em;
}
#ph-tabs nav {
	text-align: center;
